import { postOrderLogisticsQuery } from '@/api/userApi.js'
export default {
  name: 'freight',
  components: {

  },
  data() {
    return {
      info: ''
    }
  },
  onLoad(options) {
    let data = {
		orderId: options.orderId,
		shopId:options.shopId
	}
    let _this = this
    if (options.orderId && options.shopId) {
      postOrderLogisticsQuery(data).then(res => {
        if (res.code === '1000') {
          this.info = res.data
        } 
      })
    }
  },
  methods: {
    goBack(_this) {
      uni.showModal({
      	title: '提示',
      	content: '数据错误，返回上一页?',
      	success: res => {
			uni.navigateBack({
				delta:1
			})
		}
      });
    }
  }
}
